.main {
  &-passkey-create {
    display: flex;
    flex-direction: column;
    margin: 10px;
    &-btn {
      margin: 10px auto;
      padding: 0;
      border: none;
      font: inherit;
      background-color: transparent;
      cursor: pointer;
      color: blue;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &-btn {
    width: 120px;
    height: 30px;
    margin: 20px auto;
  }
}
